import { auth } from "@/auth"
import { PrismaClient, Guest } from "@prisma/client"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table" // Need to install table
import { Users, CheckCircle, XCircle, Clock } from "lucide-react"

const prisma = new PrismaClient()

async function getStats() {
    const totalGuests = await prisma.guest.count()
    const attending = await prisma.guest.count({ where: { rsvpStatus: 'ACCEPTED' } })
    const declined = await prisma.guest.count({ where: { rsvpStatus: 'DECLINED' } })
    const pending = await prisma.guest.count({ where: { rsvpStatus: 'PENDING' } })

    return { totalGuests, attending, declined, pending }
}

// Temporary interface to handle stale Prisma types in IDE
interface GuestWithAddress extends Guest {
    address: string | null
    city: string | null
    state: string | null
    zip: string | null
}

async function getGuests(): Promise<GuestWithAddress[]> {
    const guests = await prisma.guest.findMany({
        orderBy: { lastName: 'asc' }
    })
    return guests as unknown as GuestWithAddress[]
}

export default async function AdminDashboard() {
    const session = await auth()
    if (!session) return <div>Unauthorized</div>

    const stats = await getStats()
    const guests = await getGuests()

    return (
        <div className="p-8 bg-gray-50 min-h-screen">
            <div className="flex justify-between items-center mb-8">
                <h1 className="text-3xl font-bold">Dashboard</h1>
                <div className="text-sm text-gray-500">Welcome, {session.user?.name}</div>
            </div>

            {/* Stats Cards */}
            <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Total Invited</CardTitle>
                        <Users className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">{stats.totalGuests}</div>
                    </CardContent>
                </Card>
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Attending</CardTitle>
                        <CheckCircle className="h-4 w-4 text-green-500" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">{stats.attending}</div>
                    </CardContent>
                </Card>
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Declined</CardTitle>
                        <XCircle className="h-4 w-4 text-red-500" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">{stats.declined}</div>
                    </CardContent>
                </Card>
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Pending</CardTitle>
                        <Clock className="h-4 w-4 text-yellow-500" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">{stats.pending}</div>
                    </CardContent>
                </Card>
            </div>

            {/* Guest List */}
            <Card>
                <CardHeader>
                    <CardTitle>Guest List</CardTitle>
                </CardHeader>
                <CardContent>
                    <div className="relative w-full overflow-auto">
                        <Table>
                            <TableHeader>
                                <TableRow>
                                    <TableHead>Name</TableHead>
                                    <TableHead>Email</TableHead>
                                    <TableHead>Address</TableHead>
                                    <TableHead>City</TableHead>
                                    <TableHead>State</TableHead>
                                    <TableHead>Zip</TableHead>
                                    <TableHead>Status</TableHead>
                                    <TableHead>Meal</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {guests.map((guest) => (
                                    <TableRow key={guest.id}>
                                        <TableCell className="font-medium">{guest.firstName} {guest.lastName}</TableCell>
                                        <TableCell>{guest.email}</TableCell>
                                        <TableCell>{guest.address || '-'}</TableCell>
                                        <TableCell>{guest.city || '-'}</TableCell>
                                        <TableCell>{guest.state || '-'}</TableCell>
                                        <TableCell>{guest.zip || '-'}</TableCell>
                                        <TableCell>
                                            <span className={`inline-flex items-center rounded-full px-2.5 py-0.5 text-xs font-medium ${guest.rsvpStatus === 'ACCEPTED' ? 'bg-green-100 text-green-800' :
                                                guest.rsvpStatus === 'DECLINED' ? 'bg-red-100 text-red-800' :
                                                    'bg-yellow-100 text-yellow-800'
                                                }`}>
                                                {guest.rsvpStatus}
                                            </span>
                                        </TableCell>
                                        <TableCell>{guest.mealPreference || '-'}</TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </div>
                </CardContent>
            </Card>
        </div>
    )
}
